import React, { Fragment, useState } from "react";
import { useForm } from "react-hook-form";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Toast from "react-bootstrap/Toast";
import ToastContainer from "react-bootstrap/ToastContainer";
import Moment from "react-moment";
import Content from "../content/Content";
import "./form.css";

const messages = {
  required: "Disculpe, pero este campo es requerido 😁",
  name: "El formato introducido no es el correcto",
  mail: "Debes introducir una dirección correcta",
  phone: "Debes introducir un número correcto",
};

const Forms = () => {
  const [show, setShow] = useState(false);
  const [showBG, setBGShow] = useState("light");
  const [showMS, setMSShow] = useState("");
  const [showFCH, setFCHShow] = useState("");
  const [showtDes, setDesShow] = useState(
    "La nota fue creada con éxito ❤️ y siga usando la aplicación."
  );
  const [showActividad, setActividadShow] = useState("");
  const [showDescription, setDescriptionShow] = useState("");
  const [showURL, setURLShow] = useState("");
  const [show_id, set_idShow] = useState("");
  const [showBTN, setBTNShow] = useState(true);
  const [showSearchNotesNotes, setSearchNotes] = useState([]);

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    defaultValues: {
      name_actividad: showActividad,
      description: showDescription,
      url: showURL,
    },
  });

  const onSubmit = async (userInfo) => {
    if (show_id.length === 0) {
      await fetch("http://206.189.194.101:5000/server/createnotes", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(userInfo),
      })
        .then((response) => response.json())
        .then((data) => {
          setShow(true);
          setMSShow("La creación fue con éxito.❤️");
          setFCHShow(data.createdAt);
          set_idShow("");
          setActividadShow("");
          setDescriptionShow("");
          setURLShow("");
          reset({
            name_actividad: "",
            description: "",
            url: "",
          });
        })
        .catch((error) => {
          setShow(true);
          setBGShow("danger");
          setMSShow("Error a crear la nota 😢");
          setDesShow("Perdone, pero no se creó la nota correctamente.😢");
        });
    } else {
      //console.log(userInfo)
      await fetch(`http://206.189.194.101:5000/server/updatenotes/${show_id}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(userInfo),
      })
        .then((response) => response.json())
        .then((data) => {
          setShow(true);
          setMSShow("La actualización fue con éxito.❤️");
          setDesShow(
            "La nota fue actualización con éxito ❤️ y siga usando la aplicación."
          );
          setFCHShow(data.createdAt);
          set_idShow("");
          setActividadShow("");
          setDescriptionShow("");
          setURLShow("");
          setBTNShow(true);
          reset({
            name_actividad: "",
            description: "",
            url: "",
          });
        })
        .catch((error) => {
          setShow(true);
          setBGShow("danger");
          setMSShow("Error a actulizar la nota 😢");
          setDesShow("Perdone, pero no se actualizar la nota correctamente.😢");
        });
    }
  };

  const contents = (content) => {
    reset({
      name_actividad: content.name_actividad,
      description: content.description,
      url: content.url,
    });
    set_idShow(content._id);
    setActividadShow(content.name_actividad);
    setDescriptionShow(content.description);
    setURLShow(content.url);
    setBTNShow(false);
  };
  const resetForm = (e) => {
    e.preventDefault();
    set_idShow("");
    setActividadShow("");
    setDescriptionShow("");
    setURLShow("");
    setBTNShow(true);
    reset({
      name_actividad: "",
      description: "",
      url: "",
    });
  };

  const deletecontents = () => {
    set_idShow("");
    setActividadShow("");
    setDescriptionShow("");
    setURLShow("");
    setBTNShow(true);
  };

  const searchNotes = async (event) => {
    if (String(event.target.value).length > 0) {
      await fetch("http://206.189.194.101:5000/server/searchnotes", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          name_actividad: String(event.target.value).trim(),
        }),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data.noFoundTF === false) {
            setSearchNotes([data]);
          } else {
            setSearchNotes([...data]);
          }
        })
        .catch((error) => {
          setSearchNotes([]);
        });
    } else {
      setSearchNotes([]);
    }
  };

  return (
    <Fragment>
      <br></br>
      <Card className="containerForm">
        <Card.Body>
          <Card.Title>Nombre de la actividad 😍</Card.Title>
          <Form onSubmit={handleSubmit(onSubmit)}>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Nombre de la actividad 😊</Form.Label>
              <Form.Control
                type="text"
                name="name_actividad"
                placeholder="Nombre de la actividad 👩‍💻👀"
                {...register("name_actividad", {
                  required: messages.required,
                })}
                value={showActividad}
                onChange={(ev) => setActividadShow(ev.target.value)}
              />
              <Form.Text className="text-danger">
                {errors.name_actividad && errors.name_actividad.message}
              </Form.Text>
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label>Descripción de la actividad 👩‍💻</Form.Label>
              <Form.Control
                type="text"
                name="description"
                placeholder="Descripción de la actividad 🙈😍"
                {...register("description", {
                  required: messages.required,
                })}
                value={showDescription}
                onChange={(ev) => setDescriptionShow(ev.target.value)}
              />
              <Form.Text className="text-danger">
                {errors.description && errors.description.message}
              </Form.Text>
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label>Imagenes 🙈</Form.Label>
              <Form.Control
                type="url"
                name="url"
                placeholder="Ingrese la ruta de la imagen. 🖼️"
                {...register("url", {
                  required: messages.required,
                })}
                value={showURL}
                onChange={(ev) => setURLShow(ev.target.value)}
              />
              <Form.Text className="text-danger">
                {errors.url && errors.url.message}
              </Form.Text>
            </Form.Group>
            {showBTN ? (
              <Button type="submit" variant="success">
                Crear la nota ❤️
              </Button>
            ) : (
              <Button type="submit" variant="success">
                Guardar los cambios ❤️
              </Button>
            )}

            <Button
              className="mx-2"
              onClick={(reset) => resetForm(reset)}
              variant="danger"
            >
              Borrar la nota 😰
            </Button>
          </Form>
          <Form className="my-4">
            <Form.Label>Buscador 🙈🔍</Form.Label>
            <Form.Control
              onChange={(event) => searchNotes(event)}
              type="text"
              placeholder="Escriba el nombre de la nota 🙈🔍"
            />
          </Form>

          <ToastContainer position="top-end" className="p-3">
            <Toast
              onClose={() => setShow(false)}
              show={show}
              delay={3000}
              autohide
              bg={showBG}
            >
              <Toast.Header>
                <img
                  src="/notes_personals.png"
                  style={{ with: "20px", height: "20px" }}
                  className="rounded me-2"
                  alt="Notes personals"
                />
                <strong className="me-auto">{showMS}</strong>
                <small>
                  <Moment fromNow>{showFCH}</Moment>
                </small>
              </Toast.Header>
              <Toast.Body className="Danger">{showtDes}</Toast.Body>
            </Toast>
          </ToastContainer>
        </Card.Body>
      </Card>
      <Content
        updateContent={show}
        contents={contents}
        deletecontents={deletecontents}
        showSearchNotesNotes={showSearchNotesNotes}
      />
    </Fragment>
  );
};

export default Forms;
