import React, { Fragment, useState, useEffect } from "react";
import ThemeProvider from "react-bootstrap/ThemeProvider";
import Button from "react-bootstrap/Button";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Moment from "react-moment";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
import Alert from "react-bootstrap/Alert";

import "./Content.css";
const MySwal = withReactContent(Swal);

const Content = ({
  updateContent,
  contents,
  deletecontents,
  showSearchNotesNotes,
}) => {
  const [showNotes, setNotes] = useState([]);
  const [showNoSearchNotes, setNoSearchNotes] = useState("");
  const showLoading = async () => {
    await fetch("http://206.189.194.101:5000/server/")
      .then((response) => response.json())
      .then((data) => {
        setNoSearchNotes('')
        setNotes([...data]);
      });
  };

  if (updateContent) {
    showLoading();
  }

  useEffect(() => {
    if (showSearchNotesNotes.length > 0) {
      if (showSearchNotesNotes[0].noFoundTF === undefined) {
        setNotes([...showSearchNotesNotes]);
      } else {
        setNoSearchNotes(showSearchNotesNotes[0].noFound);
        setNotes([]);
        console.log(showSearchNotesNotes);
      }
    } else {
      showLoading();
    }
  }, [showSearchNotesNotes]);

  const deleteNotes = (id) => {
    MySwal.fire({
      customClass: {
        confirmButton: "btn btn-success",
        cancelButton: "btn btn-danger",
      },
      title: "Estas seguro de eliminar la nota?",
      text: "No podrás revertir esto.!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: "Si, eliminar la nota!",
      cancelButtonText: "No, cancelar!",
      reverseButtons: true,
    }).then((result) => {
      if (result.isConfirmed) {
        MySwal.fire("Eliminado!", "Su nota ha sido borrada.", "success");
        fetch("http://206.189.194.101:5000/server/deletenotes/" + id, {
          method: "DELETE",
        })
          .then((res) => res.json()) // or res.json()
          .then((res) => {
            //console.log(res);
            fetch("http://206.189.194.101:5000/server/")
              .then((response) => response.json())
              .then((data) => setNotes([...data]));
          });
      } else if (
        /* Read more about handling dismissals below */
        result.dismiss === Swal.DismissReason.cancel
      ) {
        MySwal.fire("Cancelled", "Su nota está a salvo :)", "error");
      }
    });
  };

  return (
    <>
      <br></br>
      <ThemeProvider
        breakpoints={["xxxl", "xxl", "xl", "lg", "md", "sm", "xs", "xxs"]}
        minBreakpoint="xxs"
      >
        {showNoSearchNotes ? 
          <Alert variant="warning">{showNoSearchNotes}</Alert>
         : 
          false
        }
        <Row>
          {showNotes.map((result) => (
            <Col
              style={{ marginBottom: "20px" }}
              md={12}
              lg={6}
              xl={4}
              xxl={4}
              key={result._id}
            >
              <Card style={{ marginTop: "37px", height: "100%" }}>
                <Card.Img
                  variant="top"
                  src={result.url}
                  alt="notas"
                  width="350"
                  height="400"
                />
                <Card.Body className="flexs">
                  <Card.Title>{result.name_actividad} 👀</Card.Title>
                  <Card.Text>{result.description} </Card.Text>
                  <section className="flexBt">
                    <Button variant="warning" onClick={() => contents(result)}>
                      <i className="fa-sharp fa-solid fa-file-pen"></i>
                    </Button>
                    <Button
                      onClick={() => {
                        deleteNotes(result._id);
                        deletecontents();
                      }}
                      className="mx-3 "
                      variant="danger"
                    >
                      <i className="fa-sharp fa-solid fa-eraser"></i>
                    </Button>
                  </section>
                </Card.Body>
                <Card.Footer className="text-muted">
                  <Moment fromNow>{result.createdAt}</Moment> 🕛
                </Card.Footer>
              </Card>
            </Col>
          ))}
        </Row>
      </ThemeProvider>
    </>
  );
};

export default Content;
